@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap");
.App {
  display: flex;
  min-height: 100vh;
}

::-webkit-scrollbar {
  width: 0px;
}

::-webkit-scrollbar-thumb {
  background: rgba(136, 136, 136, 0.281);
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}
